.nav_navbar {
  width: 100%;
  padding: 2rem;


  @include mq($until: desktop-841) {
    padding: 1.5rem;
    background: rgba(4, 20, 111, .8);
    transition: background 0.3s linear;
    position: fixed;
    z-index: 999;
  }

  @include mq($until: mobile-415) {
    padding: 1.25rem;
  }
}

a.logo_white {
  color: $white;
  font-size: 3rem;

  @include mq($until: desktop) {
    font-size: 2.75rem;
  }

  @include mq($until: desktop-841) {
    font-size: 2rem;
  }

  &:hover {
    color: $white;
  }
}

a.logo_standard {
  display: block;

  >img {
    display: block;
    height: 40px;
    margin-top: .5rem;

    @include mq($until: desktop-841) {
      margin-top: 0rem;
    }
  }
}

.nav_menu_item {
  padding: 0 1rem;
  line-height: 1rem;

  @include mq($until: desktop) {
    padding: 0 .5rem;
  }
}

li.nav_menu_main_list {
  display: inline-block;
  color: $white;

  >a.nav_click {
    color: $white;
    transition: .3s;
    display: block;
    padding-bottom: 1rem;
    font-weight: normal;
    -webkit-font-smoothing: unset;
    position: relative;

    @include mq($until: desktop) {
      font-size: .875rem;
    }

    &.icon_arrow {
      padding-right: 22px;
    }

    &.icon_arrow::after {
      position: absolute;
      top: 2px;
      right: 0;
    }
  }

  >a.nav_click_black {
    color: $black;
    transition: .3s;
    display: block;
    padding-bottom: 1rem;
  }

  &+li.nav_menu_main_list {
    padding-left: 2rem;

    @include mq($until: desktop) {
      padding-left: 1rem;
    }
  }
}

li.nav_menu_main_list {
  &:hover {
    >a.nav_click {
      color: #4880ff;
      transition: .3s;
    }
  }
}

.icon_arrow {
  font-weight: 500;

  &::after {
    display: inline-block;
    vertical-align: middle;
  }
}

// sub-nav

.nav-sub {
  position: relative;
  display: block;
}

ul.nav-dropdown {
  position: absolute;
  width: 10rem;
  background-color: $white;
  padding: 1.5rem;
  text-align: center;
  z-index: 5;
  border-top: 4px solid $sky-blue;
  -webkit-box-shadow: 1px 4px 18px 2px #04146f;
  box-shadow: 1px 4px 18px 2px #04146f;
  display: none;

  animation: slide 0.1s linear 0s normal;

  @include mq($until: desktop) {
    width: 8rem;
    padding: 1rem;
  }

  >li {

    >a {
      color: $black;
      line-height: 3;
      display: block;

      @include mq($until: desktop) {
        font-size: .875rem;
        line-height: 2.5;
      }

      &:hover {
        color: $sky-blue;
      }
    }
  }
}

ul.nav-dropdown_light {
  -webkit-box-shadow: 1px 4px 18px 2px #04146f26;
  box-shadow: 1px 4px 18px 2px #04146f26;
}

ul.nav-dropdown {
  &:hover {
    animation: slide 0.1s linear 0s normal;
  }
}

li.nav_menu_main_list {
  &:hover {
    ul.nav-dropdown {
      display: block;
    }
  }
}

// 語言版本
//
li.nav_lag_group {
  position: relative;
}

ul.nav_lag-sub {
  display: none;
  position: absolute;
  background-color: $white;
  padding: 1.25rem;
  border-top: 4px solid $sky-blue;
  -webkit-box-shadow: 1px 4px 18px 2px #04146f;
  box-shadow: 1px 4px 18px 2px #04146f;

  >li {
    font-size: .75rem;
    color: #4880ff;
    line-height: 1.6;
  }
}

ul.nav_lag-sub-light {
  -webkit-box-shadow: 1px 4px 18px 2px #04146f26;
  box-shadow: 1px 4px 18px 2px #04146f26;
}

li.nav_lag_group {
  display: inline-block;

  a.nav_lag_link {
    color: $white;
    font-weight: 200;
    font-size: .875rem;
    transition: .3s;
    display: block;
    padding-bottom: 1rem;

    &::after {
      content: '';
      display: inline-block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 6px 4px 0 4px;
      border-color: $white transparent transparent transparent;
      margin-left: .5rem;
      vertical-align: middle;
      transition: .3s;
    }
  }

  a.nav_lag_link_black {
    color: $black;

    &::after {
      content: '';
      display: inline-block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 6px 4px 0 4px;
      border-color: $black transparent transparent transparent;
      margin-left: .5rem;
      vertical-align: middle;
      transition: .3s;
    }
  }
}

li.nav_lag_group {
  &:hover {
    a.nav_lag_link {
      color: #4880ff;
      transition: .3s;

      &::after {
        border-color: $sky-blue transparent transparent transparent;
        border-style: solid;
        border-width: 6px 4px 0 4px;
      }
    }
  }
}

li.nav_lag_group {
  &:hover {
    ul.nav_lag-sub {
      display: block;
    }
  }
}

a.nav_lag_link {
  &::before {
    content: "";
    display: inline-block;
    width: 1px;
    height: 1rem;
    background-color: #979797;
    vertical-align: middle;
    margin-right: 1rem;
  }
}

ul.nav_lag-sub {
  animation: slide 0.1s linear 0s normal;

  li {
    a {
      line-height: 2;
    }

    a.link-active {
      color: #04146f;
    }
  }
}

ul.nav_lag-sub {
  &:hover {
    animation: slide 0.1s linear 0s normal;
  }
}

@keyframes slide {
  from {
    transform: rotateY(90deg) skewX(0deg);
  }

  to {
    transform: rotateY(0deg) skewX(0deg);
  }
}

