// start
// Menu to be toggled.
.menu {
  width: 100%;
  position: fixed;
  top: 0%;
  background-color: rgba(255, 255, 255, 0.95);
  text-align: center;
  box-shadow: 0 0 7px rgba(128, 128, 128, 0.5);
  height: 100%;
  left: 100%;
  padding-top: 80px;
  transform: translateX(100%);
  transition: 0.4s ease-in-out;
  z-index: 2;
  max-height: 100vh;
  overflow: scroll;
  padding-left: 15px;
  padding-right: 15px;

  &__link {
    padding: 10px;
    color: rgba(0, 0, 0, 0.4);
    position: relative;
    transition: 0.3s ease-in-out;
    padding: 10px 24px 10px 10px;
    display: block;
  }

  & li {
    list-style-type: none;
    position: relative;
    text-align: center;
    transition: 0.3s ease-in-out;
  }
}

// Menu toggler: checkbox.
#menu-checkbox {
  display: block;
  width: 50px;
  height: 50px;
  position: fixed;
  top: 30px;
  right: 40px;
  cursor: pointer;
  opacity: 0;
  z-index: 4;

  @include mq($until: desktop-841) {
    top: 15px;
    right: 25px;
  }
}

// Burger.
.burger__element {
  display: block;
  position: fixed;
  top: 50px;
  right: 50px;
  background-color: map-get($color-blue-map, blue-400);
  width: 30px;
  height: 2px;
  transition: 0.2s ease-in-out;
  z-index: 5;
  cursor: pointer;
  border-radius: 3px;

  @include mq($until: desktop-841) {
    top: 35px;
    right: 32px;

  }

  &::before,
  &::after {
    content: '';
    position: absolute;
    left: 0;
    width: 30px;
    height: 2px;
    border-radius: 3px;
    background-color: map-get($color-blue-map, blue-400);
  }

  &::before {
    top: 10px;
  }

  &::after {
    top: -10px;
  }
}



//次頁漢堡
.burger__element-dark {
  @include mq($until: desktop-841) {
    background-color: map-get($color-blue-map, blue-600);
    top: 48px;
    right: 32px;

    @include mq($until: mobile) {
      top: 36px;
    }

    &::before,
    &::after {
      background-color: map-get($color-blue-map, blue-600);
    }
  }

}

#menu-checkbox:checked~.burger__element {
  transform: rotate(180deg);
  transition: 0.3s ease-in-out;
}

#menu-checkbox:checked~.burger__element::before {
  width: 20px;
  top: 3px;
  left: -7px;
  transform: rotate(45deg) translateX(5px);
  transition: 0.3s ease-in-out;
}

#menu-checkbox:checked~.burger__element::after {
  width: 20px;
  top: 2px;
  left: -12px;
  -webkit-transform: rotate(-45deg) translateX(-5px);
  transform: rotate(-45deg) translateX(12px);
  transition: 0.3s ease-in-out;
}

#menu-checkbox:checked~.menu {
  transform: translateX(-100%);
}

