.padding-space-down {
  width: 20%;

  @include mq($until: desktop-877) {
    width: 10%;
  }

  @include mq($until: leptop-769) {
    background: linear-gradient(to bottom, white 0%, white 20%, #f8f8f9 20%, #f8f8f9 100%);
    width: 0%;
  }

  @include mq($until: mobile-541) {
    background: linear-gradient(to bottom, white 0%, white 15%, #f8f8f9 15%, #f8f8f9 100%);

  }


}

.list-download {
  background-color: $white;
  padding: 3rem;

  @include mq($until: desktop-877) {
    padding-top: 3rem;
    padding-left: 1rem;
  }

  @include mq($until: leptop-769) {
    padding: 0rem;
  }
}


.padding-space-width-white {
  background-color: $white;
}

.title-download {
  width: 30%;
  align-self: stretch;

  @include mq($until: leptop-769) {
    width: 100%;
    min-height: 0rem;
  }
}

.list-download {
  width: 70%;

  @include mq($until: leptop-769) {
    width: 100%;
    padding-top: 1rem;
  }

  @include mq($until: mobile-415) {
    flex-direction: column;
  }
}

h4.title-download-txt {
  font-size: 1.5rem;
  color: map-get($color-blue-map, blue-400);
  line-height: 1.8;
  padding-top: 3rem;
  padding-bottom: 5rem;
  padding-left: 2rem;

  @include mq($until: wide-1281) {
    font-size: 1.25rem;
    padding-left: 0rem;
  }

  @include mq($until: leptop-769) {
    background-color: #ffffff;
    padding-bottom: 2rem;


    padding-left: 2rem;
    display: block;

    padding: 0rem;
    text-align: left;

    padding: 2rem;
  }

  @include mq($until: leptop-649) {
    text-align: center;
  }

  @include mq($until: mobile-592) {
    padding-left: 2rem;
    padding-top: 2rem;
  }

  @include mq($until: mobile-541) {
    font-size: 1rem;
  }

  @include mq($until: mobile-415) {
    padding: 1rem;
    padding-top: 2rem;
    font-size: 1.25rem;
  }

  @include mq($until: mobile-376) {
    font-size: 1rem;
  }

  >span {
    display: block;
  }
}

.list-download-group {
  display: flex;
  width: 100%;
  padding-left: 2rem;

  @include mq($until: leptop-769) {
    padding: 1rem 2rem;
    text-align: center;
  }

  @include mq($until: leptop-649) {
    flex-direction: column;
    padding: 0rem 2rem 2rem 2rem;

  }

  @include mq($until: mobile-415) {
    flex-direction: column;
    padding-left: 1rem;
  }

}

.download-part {
  text-align: left;

  @include mq($until: leptop-769) {
    width: 50%;
  }

  @include mq($until: leptop-649) {
    width: 100%;
  }

  &+.download-part {
    padding-left: 4rem;

    @include mq($until: leptop-769) {
      padding-left: 0rem;
    }



  }
}

a.btn_load {
  display: block;
  line-height: 3;
  font-size: .875rem;
  text-decoration: underline !important;
  cursor: pointer;
  transition: .3s;
  text-align: left;
  line-height: 1.6;
  padding-bottom: 1rem;
  letter-spacing: .1rem;
  color: #1a051d;

  @include mq($until: leptop-649) {
    text-align: center;
  }


  &:hover {
    font-weight: $font-weight-bold;
    transition: .3s;
  }
}