//SMS color system
//
//gray scale
//dark to light

$black: #1a051d;
$gray: #C9C9C9;
$white: #fff;

//blue scale
//dark to light

$blue-600: #04146F;
$blue-500: #1F129F;
$blue-400: #003bde;
$blue-300: #3B26D1;
$blue-200: #7D87CC;
$blue-100: #DADAEC;

$color-blue-map:(blue-600: $blue-600,
  blue-500: $blue-500,
  blue-400: $blue-400,
  blue-300: $blue-300,
  blue-200: $blue-200,
  blue-100: $blue-100,
);

$red: #FF0050 !default;
$sky-blue: #4880ff;
$turkish-blue: #4AD5DC;
$gray-blue: #EFEFF4;

$primary: $blue-400;
$secondary: $red;
$danger: $red !default;
$success: $turkish-blue !default;
$body-bgcolor: $gray-blue;


// KVB Font family

// Fonts
//
// Font, line-height, and color for body text, headings, and more.

// stylelint-disable value-keyword-case
$font-family-sans-serif: "Noto Sans SC",
"Noto Sans TC",
"Roboto",
"Roboto Condensed",
Arial,
Helvetica,
sans-serif,
微軟正黑體,
"Segoe UI",
"Helvetica Neue",
"Apple Color Emoji",
"Segoe UI Emoji",
"Segoe UI Symbol",
"Noto Color Emoji" !default;


$font-family-sans-serif-tc: "Noto Sans SC",
"Noto Sans TC",
"Roboto Condensed",
"Roboto",
Arial,
Helvetica,
sans-serif,
微軟正黑體,
"Segoe UI",
"Helvetica Neue",
"Apple Color Emoji",
"Segoe UI Emoji",
"Segoe UI Symbol",
"Noto Color Emoji" !default;

$font-eng: "Roboto Condensed",
"Roboto",
"Noto Sans SC",
"Noto Sans TC",
Arial,
Helvetica,
sans-serif,
微軟正黑體,
"Segoe UI",
"Helvetica Neue",
"Apple Color Emoji",
"Segoe UI Emoji",
"Segoe UI Symbol",
"Noto Color Emoji" !default;

$font-family-base: $font-family-sans-serif !default;
$font-family-base-tc: $font-family-sans-serif-tc !default; //繁體字型

//font size
//
$font-size-base: 1rem !default; // Assumes the browser default, typically `16px`
$font-size: 16px !default;

$font-size-lg: ($font-size-base * 1.25) !default;
$font-size-sm: ($font-size-base * .875) !default;

$font-weight-light: 300 !default;
$font-weight-normal: 400 !default;
$font-weight-bold: 700 !default;

$font-weight-base: $font-weight-normal !default;
$line-height-base: 1.5 !default;

$h1-font-size: $font-size-base * 2.5 !default;
$h2-font-size: $font-size-base * 1.5 !default;
$h3-font-size: $font-size-base * 1.35 !default;
$h4-font-size: $font-size-base * 1.25 !default;
$h5-font-size: $font-size-base * 1.15 !default;
$h6-font-size: $font-size-base !default;

// $headings-margin-bottom: ($spacer / 2) !default;
$headings-font-family: inherit !default;
$headings-font-weight: 700 !default;
$headings-line-height: 1.2 !default;
$headings-color: inherit !default;

$display1-size: 3.25rem !default;
$display2-size: 2rem !default;
$display3-size: 4.5rem !default;
$display4-size: 3.5rem !default;

$display1-weight: 300 !default;
$display2-weight: 300 !default;
$display3-weight: 300 !default;
$display4-weight: 300 !default;
$display-line-height: $headings-line-height !default;

$small-font-size: 80% !default;