.section-sub-inner {
  max-width: 1280px;
  margin: 0 auto;
  background-color: $white;
  padding: 4rem 5rem;
  display: flex;
  justify-content: flex-start;

  @include mq($until: leptop-769) {
    padding: 3rem 3rem;
    flex-direction: column;
  }

  @include mq($until: mobile-415) {
    padding: 1rem 2rem;
  }
}

.section-illustration {
  width: 100%;
  height: auto;

  >img {
    max-width: 100%;
    height: auto;

  }
}

.section-illustration-privacy {
  width: 50%;

  @include mq($until: leptop-769) {
    text-align: center;
    width: 100%;
  }

  >img {
    max-width: 100%;
    height: auto;

    @include mq($until: leptop-769) {
      align-self: center;
      width: 50%;
    }

    @include mq($until: mobile-415) {
      width: 100%;
    }
  }


}

img.img-l {
  max-width: 100%;
  height: auto;
}

.section-txt {
  width: 50%;

  >p {
    line-height: 2;

    @include mq($until: leptop-769) {
      font-size: .875rem;
      line-height: 1.8;
    }

  }


  @include mq($until: leptop-769) {
    width: 100%;
  }
}

.ol-list {
  padding-top: 2rem;
  margin-left: 1.5rem;

  >li {
    line-height: 2;
    font-size: .875rem;
    letter-spacing: .15rem;
    font-weight: $font-weight-bold;
  }
}