// Company Mission
.section-mission,
.section-accreditation,
.section-responsibility {
  display: flex;
  flex-direction: row;
}

.padding-space {
  min-height: 30rem;
  background: -moz-linear-gradient(top, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(248, 248, 249, 1) 50%, rgba(248, 248, 249, 1) 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(255, 255, 255, 1)), color-stop(50%, rgba(255, 255, 255, 1)), color-stop(50%, rgba(248, 248, 249, 1)), color-stop(100%, rgba(248, 248, 249, 1)));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(248, 248, 249, 1) 50%, rgba(248, 248, 249, 1) 100%);
  background: -o-linear-gradient(top, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(248, 248, 249, 1) 50%, rgba(248, 248, 249, 1) 100%);
  background: -ms-linear-gradient(top, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(248, 248, 249, 1) 50%, rgba(248, 248, 249, 1) 100%);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(248, 248, 249, 1) 50%, rgba(248, 248, 249, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#f8f8f9', GradientType=0);

  @include mq($until: mobile-415) {
    background: linear-gradient(to bottom, #f8f8f9);
    background-color: #f8f8f9;
  }
}

.content-inner-s {
  margin: 0 auto;
  max-width: 1120px;
  padding: 5rem 2.25rem;

  @include mq($until: leptop-728) {
    padding: 3rem 2.25rem;
  }
}

.section-inner-flex {
  display: flex;
  flex-direction: row;
  overflow: hidden;

  @include mq($until: wide-1115) {
    flex-direction: column;
    align-items: center;
  }
}

.padding-space-width {
  width: 20%;

  @include mq($until: wide-1980) {
    width: 10%;
  }
}

.content-block-txt {
  width: 40%;
  padding-right: 2.25rem;

  @include mq($until: wide-1281) {
    width: 50%;
  }

  @include mq($until: wide-1115) {
    order: 2;
    width: 100%;
    padding-right: 0rem;
    padding-top: 4rem;
  }

  @include mq($until: mobile-541) {
    padding-top: 3rem;
  }
}

.content-block-img {
  width: 60%;
  padding-top: 5rem;

  @include mq($until: wide-1115) {
    width: 100%;
    padding-top: 2rem;
    text-align: center;
  }

  @include mq($until: mobile-491) {
    padding-top: 0rem;
  }


  >img {
    max-width: 600px;
    height: auto;

    @include mq($until: wide-1115) {
      height: 320px;
      order: 1;
    }

    @include mq($until: leptop-728) {
      height: 250px;
    }

    @include mq($until: mobile-541) {
      height: 220px;
    }

    @include mq($until: mobile-491) {
      height: 180px;
    }

    @include mq($until: mobile-491) {
      height: 160px;
    }

    @include mq($until: mobile-360) {
      height: 140px;
    }

  }
}

// Accreditation

.section-accreditation {
  background-image: url('../img/img_hk.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: auto;
}

.license-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @include mq($until: leptop-728) {
    flex-direction: column;
  }
}

.img-license {
  text-align: center;

  @include mq($until: leptop-728) {
    display: flex;
    flex-direction: row;
    align-items: center;

    @include mq($until: mobile-541) {
      flex-direction: column;
    }

    &+.img-license {
      padding-top: 4rem;

      @include mq($until: mobile-541) {
        padding-top: 2rem;
      }
    }
  }

  img {
    max-width: 70%;
    height: auto;



    @include mq($until: leptop-728) {
      height: 200px;
    }

    @include mq($until: leptop-649) {
      height: 150px;
    }


  }

  .txt-license {
    padding-top: 2rem;

    @include mq($until: leptop-728) {
      text-align: left;
      padding-top: 0rem;
      padding-left: 4rem;
    }

    @include mq($until: leptop-649) {
      padding-left: 2rem;
    }

    @include mq($until: mobile-541) {
      text-align: center;
      padding-left: 0rem;
      padding-top: 1rem;
    }

    :nth-of-type(1) {
      padding-bottom: .5rem;
    }

    >p {
      font-size: .875rem;
      color: map-get($color-blue-map, blue-600);
      font-weight: $font-weight-bold;
      letter-spacing: 0;

      @include mq($until: leptop-769) {
        font-size: 1rem;
      }

      >span {
        color: map-get($color-blue-map, blue-400);
      }
    }
  }
}

.content-block-img-padding {
  padding-top: 0rem;
}

// Social Responsibility
.section-responsibility {
  
  padding: 5rem;
  padding-bottom: 131px;
  background-color: $white;

  @include mq($until: leptop-728) {
    padding: 4rem;
  }

  @include mq($until: mobile-491) {
    padding: 3rem;
  }

  @include mq($until: mobile-415) {
    padding: 2rem;
  }

  @include mq($until: mobile) {
    padding: 0rem;
  }
}

.content-inner-s-bgcolor {

  padding: 0rem;
  background-image: linear-gradient(148deg, #10118c, #0b69d4 35%, #73c1d2 72%, #d0f6c2);
  overflow: hidden;

  >.content-inner-s-bgimg {
    width: 100%;
    height: 100%;
    background-image: url(../img/img_responsibility.png);
    background-position: right center;
    background-repeat: no-repeat;
    background-size: 70%;

    @include mq($until: wide-1115) {
      background-size: 60%;
    }

    @include mq($until: desktop-877) {
      background-size: 85%;
    }

    @include mq($until: leptop-728) {
      background-size: cover;
    }


  }
}

.content-block-txt-padding {
  padding: 3rem;

  &::after {
    content: "";

  }

  >h2 {
    @include mq($until: wide-1281) {
      padding: 2rem;
    }
  }

  >p {
    @include mq($until: wide-1281) {
      padding: 0 2rem;

    }


    &+p {
      @include mq($until: wide-1281) {
        padding-bottom: 2rem;

      }
    }

  }

  @include mq($until: wide-1400) {
    padding: 2rem;
  }

  @include mq($until: wide-1281) {
    padding: 0rem;
  }

  @include mq($until: wide-1115) {
    background-color: rgba(31, 18, 159, .6);
  }
}