.header_kv {
  width: 100%;
  height: 100vmin;
  background-image: url("../img/img_bgimg.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;

  @include mq($until: desktop) {
    height: 585px;
  }

  @include mq($until: leptop-769) {
    background-image: url("../img/img_bgimg_pad.png");
    height: 1024px;
    background-position: top center;
  }

  @include mq($until: mobile-541) {
    height: 800px;
  }

  @include mq($until: mobile-415) {
    background-image: url("../img/img_bgimg_mobile.png");
    background-size: 100%;
    height: 736px;
  }
  @include mq($until: mobile-376) {
    height: 667px;
  }
  @include mq($until: mobile) {
    height: 568px;
  }

}

// wording -- slogan

.kv_wording {
  width: 100%;
  height: 100%;
  text-align: center;

}

.kv_wording_inner {
  width: 100%;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;

  @include mq($until: wide-1281) {
    top: 40%;
  }

  @include mq($until: leptop-769) {
    top: 30%;
  }

  @include mq($until: mobile-415) {
    top: 25%;
  }
}

.kv_wording_inner_slgan {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @include mq($until: leptop-769) {
    transform: translate(-50%, 50%);
  }



  >p {
    font-size: 3rem;
    color: #4ad5dc;
    font-weight: $font-weight-normal;
    letter-spacing: 2;
    padding-bottom: 1rem;
    text-shadow: 0 2px 4px #00000080;

    @include mq($until: wide-1281) {
      font-size: 2rem;
    }

    @include mq($until: desktop) {
      font-size: 1.5rem;
    }

    @include mq($until: leptop-769) {
      font-size: 2rem;
    }

    @include mq($until: mobile-541) {
      font-size: 1.5rem;
    }

    @include mq($until: mobile-415) {
      padding-bottom: 0rem;
    }
    @include mq($until: mobile-376) {
    font-size: 1.25rem;
    line-height: 3rem;
  }
    @include mq($until: mobile) {
    font-size: 1rem;
    line-height: 2.5rem;
  }
  }

  h1 {
    font-size: 4rem;
    color: $white;
    font-weight: $font-weight-bold;
    line-height: 6rem;
    letter-spacing: 0.5rem;
    text-shadow: 0 2px 4px #00000080;

    @include mq($until: wide-1281) {
      font-size: 3rem;
      line-height: 4rem;
    }

    @include mq($until: desktop) {
      font-size: 2rem;
      line-height: 3rem;
    }

    @include mq($until: leptop-769) {
      font-size: 3rem;
      line-height: 4rem;
    }

    @include mq($until: mobile-541) {
      font-size: 2rem;
      line-height: 3rem;
    }
    @include mq($until: mobile-376) {
      font-size: 1.5rem;
      line-height: 2rem;
    }
    
    @include mq($until: mobile) {
    font-size: 1.5rem;
    line-height: 2.5rem;
  }

    >span.wording_second {
      display: block;
    }
  }
}

.kv-kline {
  width: 100%;
  position: absolute;
  z-index: -1;
  top: 20%;
  left: 50%;
  transform: translate(-50%, 20%);

  @include mq($until: leptop-769) {
    transform: translate(-50%, 160%);
  }

  @include mq($until: mobile-415) {
    transform: translate(-50%, 180%);
  }

  >img {
    max-width: 40%;
    height: auto;

    @include mq($until: wide-1281) {
      max-width: 50%;
    }

    @include mq($until: leptop-769) {
      max-width: 70%;
    }

    @include mq($until: mobile-415) {
      max-width: 80%;
    }
    @include mq($until: mobile-376) {
      max-width: 90%;
    }
  }
}

.footer_map {
  >.float-right {
    float: none !important;
  }
}