* {
    box-sizing: border-box;
    margin: 0 auto;
    font-family: $font-family-sans-serif-tc; //$font-family-base
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    vertical-align: middle;
    background: 0 0;
    padding-left: 0px;
    padding-right: 0px;

    // &::before,
    // ::after {
    //     box-sizing: inherit;
    // }

    // fixed bootstrap 3 form
}

body {
    background-color: #F8F8F9;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-bottom: 0rem;
}

a {
    display: inline-block;
    vertical-align: middle;
    text-decoration: none !important;
}

p {
    margin-bottom: 0rem;
    letter-spacing: .2rem;
    letter-spacing: normal;
    line-height: 1.8;
    font-size: 1rem;
    color: #333333;
}

ul {
    margin-bottom: 0rem;

    >li {
        list-style-type: none;
    }
}

img {
    vertical-align: middle;
}

input:focus {
    outline: none;
}

::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: map-get($color-blue-map, blue-100) !important;
    opacity: 1;
    font-size: .875rem;
    /* Firefox */
}

::-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: map-get($color-blue-map, blue-100) !important;
    font-size: .875rem;
}

::-ms-input-placeholder {
    /* Microsoft Edge */
    color: map-get($color-blue-map, blue-100) !important;
    font-size: .875rem;
}

textarea:focus,
input:focus {
    color: rgb(255, 247, 149) !important;
}

