.platform-download {
  max-width: 1980px;
  padding-left: 0;
  overflow: hidden;
}

.platform {
  position: relative;
  .platform-inner {
    position: absolute;
    max-width: 700px;
    height: auto;
    right: 5%;
    @include mq($until: wide-1980) {
      right: 10%;
    }
    @include mq($until: wide-1400) {
      right: 5%;
    }
    @include mq($until: wide-1281) {
      right: 0%;
    }
    @include mq($until: desktop-841) {
      right: -3%;
    }

    @include mq($until: leptop-769) {
      max-width: 768px;
      right: 0%;
    }
    @include mq($until: mobile-592) {
      width: 100%;

    }
  }
}

    .platform-inner-box{
      width: 100%;
      height: 800px;
      display: flex;
      flex-direction: column;
      padding: 4rem;
      justify-content: center;
      @include mq($until: wide-1980) {
        height: 700px;
      }
      @include mq($until: wide-1400) {
        height: 600px;
      }
      @include mq($until: wide-1281) {
       height: 500px;
      }
      @include mq($until: wide-1081) {
       height: 400px;
      }
      @include mq($until: desktop-894) {
        padding: 2rem;
      }
      @include mq($until: desktop-841) {
        padding: 1rem;
        height: 300px;
      }
      @include mq($until: leptop-769) {
        padding: 5rem;
        width: 768px;
        height: 600px;
      }
      @include mq($until: tablet) {
        width: 100%;
      }
      @include mq($until: mobile-592) {
         height: 100%;
         padding: 6rem;
         padding-top: 4rem;
      }
       @include mq($until: mobile-415) {
         padding: 4rem;

       }
    }
    .platform-btn{
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
        @include mq($until: leptop-769) {
        justify-content: flex-start;
      }
      @include mq($until: mobile-592) {
        flex-direction: column;
      }
    }


h4.h4-platform {
  font-size: 1.5rem;
  font-weight: $font-weight-bold;
  padding-bottom: 3rem;
  @include mq($until: wide-1115) {
    font-size: 1.25rem;
    padding-bottom: 2rem;
  }
   @include mq($until: mobile-592) {
     text-align: center;
     line-height: 1.6;
   }
}


a.platform-link {
  text-align: center;
  display: block;
  padding: 3rem;
  -webkit-box-shadow: 0px 0px 0px 2px #0b69d4;
  box-shadow: 0px 0px 0px 2px #0b69d4;
  border-radius: 4px;
  vertical-align: middle;
  color: map-get($color-blue-map, blue-400);
  font-weight: $font-weight-bold;
  transition: .3s;

  @include mq($until: wide-1115) {
    padding: 2rem;
  }
  @include mq($until: wide-1081) {
    padding: 1.5rem;
  }
   @include mq($until: mobile-592) {
     background: rgba(255, 255, 255, .7);
   }

  &::before {
    content: "";
    display: inline-block;
    width: 0;
    height: 100%;
    vertical-align: middle;
  }

  &:hover {
    -webkit-box-shadow: 0px 0px 0px 2px #fff;
    box-shadow: 0px 0px 0px 2px #fff;
    background-color: $white;
    transition: .3s;
  }

  >span {
    display: block;

    &.btn-style-1 {
      color: #0b69d4;
    }
  }
}

.btn-box {
  margin-right: 3rem;
   @include mq($until: mobile-592) {
     margin-right: 0rem;
     &+.btn-box{
       margin-top: 2rem;
     }
  }
}

p.btn-style-2 {
  font-size: .75rem;
  color: map-get($color-blue-map, blue-200);
  font-weight: $font-weight-normal;
  padding-top: 10px;
  text-align: center;
  @include mq($until: mobile-592) {
   color: #FF0050;
  }
}

img.img-kv-style_platform {
  display: block;
  width: 100%;
  height: auto;
  @include mq($until: leptop-769) {
    display: none;
  }
}


img.img-kv3-style_pad{
  display: none;
  @include mq($until: leptop-769) {
    display: block;
    width: 768px;
  }
  @include mq($until: mobile-592) {
    width: 100%;
    height: 500px;
  }
  @include mq($until: mobile-541) {
    width: 100%;
    height: auto;
  }
  @include mq($until: mobile-491) {
    display: none;
  }
}

img.img-kv3-style_mobile{
  display: none;
  @include mq($until: mobile-491) {
    display: block;
    height: 491px;
  }
}