.table-time {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  @include mq($until: tablet) {
    flex-direction: column;
  }
}

// table style
table.table {
  @include mq($until: tablet) {
    margin-bottom: 0rem;
  }

}

.no-border {
  border-top: 0px solid #dee2e6 !important;
  padding: .6rem !important;
}

.border-bottom {
  border-bottom: 2px solid #dee2e6 !important;
  padding: .6rem !important;
}

th.no-border {
  font-size: 1rem;
  color: $black;
  font-weight: $font-weight-bold;
  line-height: 2rem;
}

td.no-border {
  font-size: 1.125rem;
  color: map-get($color-blue-map, blue-400);
  font-weight: $font-weight-normal;
  line-height: 2rem;

  @include mq($until: tablet) {
    line-height: 2.5rem;
  }

  @include mq($until: mobile-491) {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}

// text

h3.slogan {
  text-align: center;
  font-weight: $font-weight-bold;
  padding-bottom: 5rem;
  font-size: 1.75rem;

  @include mq($until: desktop-941) {
    font-size: 1.5rem;
    padding-bottom: 3.5rem;
  }

  @include mq($until: desktop-841) {
    font-size: 1.25rem;
    padding-bottom: 2.5rem;
  }

  @include mq($until: mobile) {
    padding-bottom: 2rem;
    font-size: 1rem;
  }

  &::before,
  &::after {
    content: "\e902";
    display: inline-block;
    color: map-get($color-blue-map, blue-400);
    font-size: 56px;
    font-weight: $font-weight-normal;

    @include mq($until: tablet) {
      font-size: 40px;
    }

    @include mq($until: mobile) {
      font-size: 24px;
    }
  }

  &::before {
    margin-right: -20px;

    @include mq($until: mobile) {
      margin-right: -10px;
    }
  }

  &::after {
    margin-left: -20px;
    transform: rotate(-180deg);

    @include mq($until: mobile) {
      margin-left: -13px;
    }
  }
}

.title-box {
  width: 35%;

  @include mq($until: leptop-769) {
    width: 40%;
  }

  @include mq($until: tablet) {
    width: 100%;
    padding-bottom: 1rem;
  }

  &.title-box-bgcolor {
    >.title-box-inner {
      text-align: center;
      min-height: 300px;
      background-color: map-get($color-blue-map, blue-400);

      @include mq($until: tablet) {
        min-height: 100px;
      }

      @include mq($until: mobile) {
        min-height: 70px;
      }

      >.title-h3 {
        color: $white;
        font-size: 1.5rem;
        line-height: 300px;

        @include mq($until: desktop-941) {
          font-size: 1.25rem;
        }

        @include mq($until: tablet) {
          line-height: 100px;
        }

        @include mq($until: mobile) {
          font-size: 1rem;
          line-height: 70px;
        }

      }
    }
  }
}

.title-table {
  width: 65%;
  padding-left: 7rem;
  padding-right: 7rem;

  @include mq($until: wide-1400) {
    padding-left: 5rem;
  }

  @include mq($until: desktop-877) {
    padding-right: 0rem;
  }

  @include mq($until: leptop-769) {
    width: 60%;
    padding-left: 2rem;
  }

  @include mq($until: tablet) {
    width: 100%;
    padding: 1rem;
  }

  @include mq($until: mobile-491) {
    padding: 0rem;
  }
}

.feature-foucs-item {
  text-align: center;
  padding: 0 4rem;

  @include mq($until: tablet) {
    padding: 0 5rem;

    &+.feature-foucs-item {
      padding-top: 3rem;
    }
  }

  @include mq($until: mobile-376) {
    &+.feature-foucs-item {
      padding-top: 1rem;
    }
  }

  @include mq($until: mobile-541) {
    padding: 0 2rem;
  }

  @include mq($until: mobile-415) {
    padding: 0 1rem;
  }

  @include mq($until: mobile-376) {
    padding-left: 0rem;
    padding-right: 0rem;
  }


  >img {
    max-height: 120px;
  }

  >.feature-foucs-txt {
    padding-top: 3rem;

    @include mq($until: desktop-941) {
      padding-top: 2rem;
    }

    @include mq($until: tablet) {
      padding-left: 1.25rem;
    }

    @include mq($until: mobile) {
      padding-top: 0rem;
    }

    >p {
      font-size: 1.25rem;
      color: $black;
      line-height: 1.8;
      font-weight: $font-weight-normal;
      text-align: left;

      @include mq($until: desktop-1025) {
        font-size: 1rem;
      }

      @include mq($until: tablet) {
        padding-left: 2rem;
      }

      >span {
        font-size: 1rem;
        color: #999999;
      }
    }
  }
}

.content-inner-s-products {
  padding: 2rem 2.25rem;

  @include mq($until: mobile-415) {
    padding: 2rem 1rem;
  }
}

.feature-foucs {
  padding-bottom: 4rem;

  @include mq($until: mobile-415) {
    padding-bottom: 2rem;
  }
}

.section-inner-flex-products {
  @include mq($until: wide-1115) {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
  }

  @include mq($until: tablet) {
    flex-direction: column;
  }

}

.feature-item {
  text-align: center;
  padding-top: 4rem;
  border-top: solid 4px #ecebed;

  @include mq($until: mobile-415) {
    padding-top: 2rem;
  }

  img {
    max-height: 112px;

    @include mq($until: mobile) {
      max-height: 96px;
    }
  }

  h3.h3-third {
    font-size: 1.125rem;
    color: map-get($color-blue-map, blue-400);
    padding: 1.5rem 0;

    @include mq($until: desktop-1025) {
      font-size: 1rem;
    }

    @include mq($until: tablet) {
      font-size: 1.5rem;
    }

    @include mq($until: mobile-376) {
      padding-top: 1rem;
      font-size: 1.25rem;
    }

  }

  .feature-item-card {
    padding: 2rem;
    width: 33.33%;

    @include mq($until: desktop) {
      padding: 1rem;
    }

    @include mq($until: tablet) {
      width: 100%;
      padding: 0rem 4rem 3rem 4rem;
    }

    @include mq($until: mobile-541) {
      padding: 0rem 2rem 2rem 2rem;
    }

    @include mq($until: mobile-415) {
      padding: 0rem 1rem 2rem 1rem;
    }

    // @include mq($until: mobile) {
    //   padding: 0rem 0rem 2rem 0rem;
    // }
  }
}

.padding-space-half {
  min-height: 30px;
  background: -moz-linear-gradient(top, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 70%, rgba(255, 255, 255, 1) 70%, rgba(255, 255, 255, 1) 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(248, 248, 249, 1)), color-stop(70%, rgba(248, 248, 249, 1)), color-stop(70%, rgba(255, 255, 255, 1)), color-stop(100%, rgba(255, 255, 255, 1)));
  background: -webkit-linear-gradient(top, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 70%, rgba(255, 255, 255, 1) 70%, rgba(255, 255, 255, 1) 100%);
  background: -o-linear-gradient(top, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 70%, rgba(255, 255, 255, 1) 70%, rgba(255, 255, 255, 1) 100%);
  background: -ms-linear-gradient(top, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 70%, rgba(255, 255, 255, 1) 70%, rgba(255, 255, 255, 1) 100%);
  background: linear-gradient(to bottom, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 70%, rgba(255, 255, 255, 1) 70%, rgba(255, 255, 255, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#f8f8f9', endColorstr='#ffffff', GradientType=0);

  @include mq($until: mobile-415) {
    background: -webkit-gradient(linear, left top, left bottom, from(#f8f8f9));
    background-color: #f8f8f9;
  }
}

.title-box-inner-bg {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

h3.title-h3-2row {
  color: $white;
  font-size: 1.5rem;
  display: block;
  line-height: 1.8;

  @include mq($until: desktop-941) {
    font-size: 1.25rem;
  }

  @include mq($until: mobile-376) {
    font-size: 1rem;
  }

  span.title-h3-break {
    display: block;

    @include mq($until: tablet) {
      display: inline-block;
    }
  }
}

.title-box-ipo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  @include mq($until: tablet) {
    align-items: flex-start;
  }

  p.title-box-ipo-txt {
    width: 100%;


    span.txt-paragraph-strong {
      font-weight: $font-weight-bold;
      display: inline;

    }
  }
}

p.txt-num1 {
  position: relative;

  &::before {
    content: "";
    background-image: url('../img/icon_num_1.png');
    background-repeat: no-repeat;
    background-size: 50%;
    height: 100px;
    width: 50%;
    position: absolute;
    left: -90px;
    top: 0;

    @include mq($until: wide-1115) {
      left: -80px;
    }

    @include mq($until: desktop-1025) {
      left: -70px;
    }

    @include mq($until: desktop-941) {
      left: -60px;
    }

    @include mq($until: tablet) {
      left: -40px;
      background-size: 40%;
    }

    @include mq($until: mobile-415) {
      left: -30px;
    }

    @include mq($until: mobile-376) {
      left: -20px;
      background-size: 35%;
    }
  }
}

p.txt-num2 {
  position: relative;

  &::before {
    content: "";
    background-image: url('../img/icon_num_2.png');
    background-repeat: no-repeat;
    background-size: 40%;
    height: 100px;
    width: 50%;
    position: absolute;
    left: -90px;
    top: 0;

    @include mq($until: desktop-1025) {
      left: -50px;
    }

    @include mq($until: leptop-769) {
      left: -70px;
    }

    @include mq($until: tablet) {
      left: -50px;
      background-size: 40%;
    }

    @include mq($until: mobile-415) {
      left: -30px;
    }

    @include mq($until: mobile-376) {
      left: -20px;
      background-size: 35%;
    }
  }
}