body {
  background-color: #f8f8f9;
}

.full-screen {
  width: 100%;
}

.full-screen-fixed {
  position: fixed;
  transition: background 0.3s linear;
  // background: #f8f8f9;
  background: rgba(248, 248, 249, .9);
  -webkit-box-shadow: 0px 10px 28px 1px rgba(0, 0, 0, 0.03);
  box-shadow: 0px 10px 28px 1px rgba(0, 0, 0, 0.03);
  z-index: 9999;
}

.screen-standard {
  max-width: 1366px;
  padding: 1.5rem;
  margin: 0 auto;

  @include mq($until: mobile) {
    padding: 1rem;
  }
}

.section_title {
  padding-top: 5rem;
}

// kv
section.img-kv {
  background-image: linear-gradient(to bottom, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 80%, rgba(255, 255, 255, 1) 80%, rgba(255, 255, 255, 1) 100%);
  background: -ms-linear-gradient(top, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 80%, rgba(255, 255, 255, 1) 80%, rgba(255, 255, 255, 1) 100%);
  background: -moz-linear-gradient(top, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 80%, rgba(255, 255, 255, 1) 80%, rgba(255, 255, 255, 1) 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(248, 248, 249, 1)), color-stop(80%, rgba(248, 248, 249, 1)), color-stop(80%, rgba(255, 255, 255, 1)), color-stop(100%, rgba(255, 255, 255, 1)));
  background: -webkit-linear-gradient(top, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 80%, rgba(255, 255, 255, 1) 80%, rgba(255, 255, 255, 1) 100%);
  background: -o-linear-gradient(top, rgba(248, 248, 249, 1) 0%, rgba(248, 248, 249, 1) 80%, rgba(255, 255, 255, 1) 80%, rgba(255, 255, 255, 1) 100%);
}

.img-kv-inner {
  max-width: 1980px;
  padding-left: 0;
}

img.img-kv-style {
  width: 100%;
  height: auto;
}

// summary
.section-summary {
  background-color: $white;
}

.content-inner {
  max-width: 1200px;
  padding: 4rem 2rem;
  margin: auto;

  @include mq($until: mobile-592) {
    padding: 2rem;
  }

  @include mq($until: mobile) {
    padding: 2rem 1.5rem;
  }
}

p.txt-paragraph {
  &+p.txt-paragraph {
    padding-top: 2rem;
  }

  >span {
    display: block;
    padding-top: 2rem;
    color: map-get($color-blue-map, blue-400);
    font-weight: $font-weight-bold;
  }
}

p.txt-paragraph-white {
  color: $white;
}

// fonts
// title : h1 style
// title : h2 style

h1.secondpage-title {
  font-size: 3rem;
  font-weight: 700;
  color: map-get($color-blue-map, blue-400);
  padding-bottom: 5rem;
  padding-top: 4rem;

  @include mq($until: wide-1400) {
    padding-bottom: 2rem;
  }

  @include mq($until: desktop-1025) {
    font-size: 2rem;
    padding-top: 3rem;
    padding-bottom: 1rem;
  }

  @include mq($until: desktop-841) {
    padding-top: 1rem;
  }

  @include mq($until: mobile) {
    padding-top: 0rem;
    padding-bottom: 0rem;
    font-size: 1.5rem;
  }


  >span {
    display: block;
    font-size: 1.75rem;
    color: $sky-blue;
    font-weight: $font-weight-light;
    padding-bottom: .6rem;
    font-family: $font-eng;
    @include mq($until: desktop-1025) {
      font-size: 1.25rem;
    }

    @include mq($until: mobile) {
      font-size: 1rem;
      padding-bottom: .3rem;
    }
  }
}

h2.secondpage-title-sub {
  font-size: 2.25rem;
  font-weight: 700;
  color: map-get($color-blue-map, blue-400);
  padding-bottom: 2rem;

  @include mq($until: desktop-1025) {
    font-size: 1.75rem;
  }


  >span {
    display: block;
    font-size: 1rem;
    color: $sky-blue;
    font-weight: $font-weight-light;
    padding-bottom: .6rem;
    font-family: $font-eng;
    @include mq($until: desktop-1025) {
      font-size: .875rem;
    }
  }
}

h2.secondpage-title-sub-white {
  color: $white;

  >span {
    color: $white;
  }
}