.enLayout {


  td.td-30,
  td.td-70 {
    font-size: .875rem;
  }

  // nav

  .darkNav {
    li.nav_menu_main_list {
      >a.nav_click {
        font-family: 'kvbsec', "Roboto Condensed" !important;
      }

    }
  }

  ul.nav-dropdown {

    width: 11rem;
    padding: 1rem;
  }

  ul.nav_lag-sub {
    width: 5.5rem;
    padding: 1rem;
  }

  ul.nav-dropdown>li>a {
    font-family: "Roboto Condensed";
    line-height: 2;
  }

  .ham_btn {
    @include mq($until: mobile-415) {
      font-size: 1rem;
    }
  }


  //index - start
  .kv_wording_inner_slgan {
    >p {

      font-size: 2rem;

      @include mq($until: wide-1281) {
        font-size: 2rem;
      }

      @include mq($until: desktop) {
        font-size: 1.5rem;
      }

      @include mq($until: leptop-769) {
        font-size: 1rem;
      }

      @include mq($until: mobile-541) {
        font-size: 1rem;
      }

      @include mq($until: mobile-415) {
        padding-bottom: 0rem;
      }

      @include mq($until: mobile-376) {
        font-size: 1.25rem;
        line-height: 3rem;
      }

      @include mq($until: mobile) {
        font-size: 1rem;
        line-height: 2.5rem;
      }
    }

    h1 {
      font-size: 2.5rem;
      font-weight: 400;
      line-height: 3rem;
      letter-spacing: 0rem;

      @include mq($until: wide-1281) {
        font-size: 2rem;
        line-height: 4rem;
      }

      @include mq($until: desktop) {
        font-size: 1.5rem;
        line-height: 2.5rem;
      }

      @include mq($until: leptop-769) {
        font-size: 1.5rem;
        line-height: 2rem;
      }

      @include mq($until: mobile-541) {
        font-size: 1.25rem;

      }

      @include mq($until: mobile-376) {
        font-size: 1.25rem;
        line-height: 2rem;
      }

      @include mq($until: mobile) {
        font-size: 1rem;
        line-height: 1.5rem;
      }
    }
  }

  .kv_wording_inner {
    @include mq($until: mobile-415) {
      top: 35%;
    }

    @include mq($until: mobile-376) {
      top: 25%;
    }
  }

  .title-box.title-box-bgcolor {
    >.title-box-inner {
      min-height: 300px;

      @include mq($until: mobile-415) {
        min-height: 80px;
      }
    }

  }

  //index - end
  //theam - start

  p {
    line-height: 2;
  }

  .content-inner {
    @include mq($until: leptop-641) {
      padding: 2rem 2rem;
    }

    @include mq($until: mobile-376) {
      padding: 2rem 1rem;
    }
  }

  .screen-standard {
    @include mq($until: mobile-415) {
      padding: 1rem;
    }

    padding: 1rem;
  }

  h1.secondpage-title {
    padding-top: 2rem;
    padding-bottom: 2rem;

    @include mq($until: desktop-894) {
      padding-top: 1rem;
      padding-bottom: 1rem;
    }

    @include mq($until: mobile-415) {
      padding-top: 0rem;
      padding-bottom: 0rem;
    }

    span {
      font-size: 2rem;
      font-weight: 700;
      color: #003bde;
      padding-bottom: 0rem;

      @include mq($until: mobile-415) {
        font-size: 1.15rem;
        padding-top: 1rem;
        padding-bottom: 1rem;
      }
    }
  }

  h2.secondpage-title-sub {
    >span {
      font-size: 1.5rem;
      font-weight: 700;
      color: #003bde;
      padding-bottom: .0rem;

      @include mq($until: mobile-415) {
        padding: 1.15rem;
        text-align: center;
        font-size: 1.15rem;
      }
    }
  }

  h2.secondpage-title-sub-white {
    >span {
      color: #fff;
    }

  }

  .section-responsibility {
    p.txt-paragraph-white {
      line-height: 2;
      font-weight: $font-weight-light;
      font-size: 0.875rem;
    }
  }

  .title-box.title-box-bgcolor {
    .title-box-inner {
      display: flex;
      justify-content: center;
      flex-flow: column;

      .title-h3 {
        line-height: 3rem;

        @include mq($until: tablet) {
          line-height: 1.6rem;
        }

        @include mq($until: mobile-415) {
          font-size: 1rem;
        }
      }
    }
  }

  .content-block-txt-padding {
    padding-bottom: 2rem;

    @include mq($until: mobile-415) {
      padding-bottom: 2rem;
    }

    >h2 {
      @include mq($until: mobile-415) {
        padding: 1rem;
      }

    }

    >p {
      @include mq($until: mobile-415) {
        padding: 0 1rem;
      }

    }
  }


  .feature-item {
    h3.h3-third {
      font-size: 1rem;
    }
  }

  .feature-item-txt {
    >p {
      font-size: 0.875rem;
    }
  }

  h3.slogan {
    font-size: 1.3rem;

    @include mq($until: desktop-941) {
      font-size: 1.25rem;
      padding-bottom: 3.5rem;
    }

    @include mq($until: desktop-841) {
      padding-bottom: 2.5rem;
    }

    @include mq($until: leptop-641) {
      font-size: 1.15rem;
    }

    @include mq($until: mobile-592) {
      line-height: 1.4rem;
    }

    @include mq($until: mobile-415) {
      line-height: 1.6;
      font-size: 1rem;
    }

    @include mq($until: mobile) {
      padding-bottom: 2rem;
      font-size: 1rem;
    }
  }

  th.no-border {
    @include mq($until: mobile-415) {
      font-size: .875rem;
    }
  }

  td.no-border {
    font-size: 1rem;

    @include mq($until: mobile-592) {
      line-height: 2rem;
      font-size: .875rem;
    }

    @include mq($until: mobile-415) {
      line-height: 3rem;
    }

    @include mq($until: mobile-376) {
      line-height: 1.6rem;
    }
  }

  .no-border {
    @include mq($until: mobile-415) {
      padding: .5rem !important;

    }
  }

  .title-table {
    @include mq($until: desktop-941) {
      padding-left: 2rem;
    }

    @include mq($until: tablet) {
      padding-left: 0rem;
    }
  }

  .feature-item {
    .feature-item-card {
      @include mq($until: leptop-641) {
        padding: 0rem 3rem 3rem 3rem;
      }

      @include mq($until: mobile-376) {
        padding: 0rem 0rem 2rem 0rem;
      }
    }
  }

  .feature-foucs-item {
    @include mq($until: leptop-641) {
      padding: 0 3rem;
      margin-bottom: 2rem;
    }

    @include mq($until: mobile-592) {
      padding: 0 1rem;
    }

    @include mq($until: mobile-376) {
      padding: 0rem;
    }
  }

  h3.slogan::before,
  h3.slogan::after {
    @include mq($until: leptop-641) {
      font-size: 30px;
    }

    @include mq($until: mobile-592) {
      display: none;
    }
  }

  h3.slogan::before {
    @include mq($until: leptop-641) {
      margin-right: -10px;
    }

    @include mq($until: mobile-541) {
      margin-right: 5px;
    }
  }

  h3.slogan::after {
    @include mq($until: leptop-641) {
      margin-left: -10px;
    }

    @include mq($until: mobile-541) {
      margin-left: 5px;
    }
  }

  tr {
    td:last-child {
      font-weight: $font-weight-bold;
    }
  }

  // QA
  .content-inner-qa {
    @include mq($until: wide-1281) {
      padding: 2rem 2rem;
    }

    >h2 {
      padding-bottom: 0rem;
    }
  }

  .question {
    p.qa-txt-title {
      font-size: 1.25rem;

      @include mq($until: desktop-941) {
        font-size: 1.15rem;
      }

      @include mq($until: mobile-415) {
        font-size: 1rem;
        font-weight: $font-weight-bold;
      }
    }
  }

  .content-inner-s-nopadding {
    @include mq($until: wide-1400) {
      width: 80%;
    }

    @include mq($until: wide-1281) {
      width: 95%;
    }

    @include mq($until: leptop-769) {
      width: 100%;
    }

    .answer {
      padding: 2.5rem 0rem 2.5rem 3rem;

      @include mq($until: mobile-415) {
        padding: 2rem;
      }
    }
  }

  .content-inner-s-nopadding {
    .question {
      @include mq($until: mobile-415) {
        padding: 1rem 2rem;
      }
    }

  }

  p.qa-txt-title {
    @include mq($until: mobile-415) {
      line-height: 1.6;
    }
  }

  // online trading


  h4.h4-platform {
    @include mq($until: wide-1400) {
      font-size: 1.25rem;
    }

    @include mq($until: wide-1115) {
      font-size: 1rem;
    }
  }

  a.platform-link {
    @include mq($until: wide-1400) {
      padding: 1rem;
    }
  }

  .btn-box {
    >p {
      line-height: 1.6;
    }
  }

  a.platform-link {
    background-color: rgba(255, 255, 255, .5);

    @include mq($until: wide-1115) {
      font-size: .875rem;
    }

    @include mq($until: leptop-728) {
      padding: 2rem;
    }

    @include mq($until: leptop-649) {
      padding: 1rem;
    }
  }

  .platform-inner-box {
    @include mq($until: mobile-415) {
      padding: 3rem;
    }
  }


  // footer
  footer.footer_main {
    padding-top: 5rem;
    max-width: 1366px;
    margin: auto;

    @include mq($until: mobile-592) {
      padding-top: 2rem;
    }

    @include mq($until: mobile-415) {
      padding-top: 2rem;
    }
  }

  .footer_menu {
    padding-top: 0rem;
    padding-bottom: 2rem;

    @include mq($until: mobile-415) {
      padding-top: 1rem;
      padding-bottom: 2rem;
    }
  }

  ul.footer_menu_list {
    li {
      a.footer_menu_font {
        @include mq($until: mobile) {
          font-size: .815rem;
          letter-spacing: -.5;
        }

      }
    }
  }

  // download

  .padding-space-down {
    @include mq($until: wide-1081) {
      width: 5%;
    }

    @include mq($until: leptop-769) {
      width: 0%;
    }
  }

  .padding-space-width-white {
    @include mq($until: wide-1081) {
      width: 5%;
    }

    @include mq($until: leptop-769) {
      width: 0%;
    }
  }

  .list-download {
    @include mq($until: leptop-769) {
      padding-left: 0rem;
      padding: 0rem;
    }
  }

  h4.title-download-txt {
    line-height: 1.6;

    @include mq($until: wide-1081) {
      padding: 1rem;
      text-align: left;
      padding-top: 2rem;
    }

    @include mq($until: leptop-769) {
      padding: 2rem 2rem 1rem 2rem;
      line-height: 1.6;
    }
  }

  .list-download-group {
    @include mq($until: wide-1081) {
      padding-left: 0rem;
      padding: 0 2rem;
    }

    @include mq($until: leptop-769) {
      padding: 1rem 2rem 2rem 2rem;
      flex-direction: column;
    }
  }

  .download-part {
    +.download-part {
      @include mq($until: leptop-769) {
        padding-left: 0rem;
      }
    }
  }

  a.btn_load:hover {
    font-weight: $font-weight-normal;
    color: #003bde;
  }

  a.btn_load {
    text-decoration: none !important;
  }

  // contact us

  .contact-card {
    @include mq($until: mobile-415) {
      height: 850px;
    }

    >.contact-card-inner {
      @include mq($until: wide-1115) {
        padding: 2rem;
      }

      @include mq($until: mobile-376) {
        padding: 2rem 1rem;
      }

    }
  }

  h4.title-download-txt-nopadding {
    @include mq($until: wide-1115) {
      // padding-left: 0rem;
      // padding-top: 0rem;
      padding-bottom: 1rem;
    }

  }

  .section-contact {
    @include mq($until: desktop-877) {
      height: 600px;
    }

    @include mq($until: leptop-769) {
      height: 500px;
    }

    @include mq($until: mobile-491) {
      height: 450px;
    }

    td.td-30 {
      @include mq($until: wide-1115) {
        width: 40%;
      }

      @include mq($until: mobile-415) {
        width: 45%;
      }

    }

    td.td-70 {
      @include mq($until: wide-1115) {
        font-weight: $font-weight-normal;
      }
    }

    h4.title-download-txt {
      @include mq($until: wide-1081) {
        padding-top: 0rem;
      }

      @include mq($until: leptop-769) {
        padding: 0rem;
        padding-bottom: 1rem;
      }

      @include mq($until: mobile-415) {
        font-size: 1rem;
      }
    }
  }

  .section-contact {
    background: url(../img/img_contact_en.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    max-width: 1280px;
    height: 952.5px;
    margin: 0 auto;
  }

  // privacy
  .ol-list {
    >li {
      letter-spacing: 0rem;
      line-height: 2;
    }
  }

}