.section-contact {
  background: url('../img/img_contact.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  max-width: 1280px;
  height: 952.5px;
  margin: 0 auto;
   @include mq($until: leptop-641) {
         height: 500px;
         background-size: 130%;
         background-position: 100% 0;
   }
   @include mq($until: mobile-415) {
     background-size: cover;
     height: 380px;
     background-position: center;
   }
   @include mq($until: mobile-376) {
     height: 360px;
   }
    @include mq($until: mobile) {
      height: 340px;
    }
}

.contact-card {
  max-width: 1280px;
  position: relative;
  height: 952.5px;
  margin-right: 3rem;
   @include mq($until: leptop-641) {
    margin-right: 0;
   }
   @include mq($until: mobile-415) {
        height: 800px;
   }
   @include mq($until: mobile-376) {
     height: 700px;
   }

  >.contact-card-inner {
    width: 50%;
    position: absolute;
    top: 30%;
    left: 40%;
    transform: translate(-30%, -40%);
    background-color: $white;
    margin-left: 20%;
    padding: 3rem;
     @include mq($until: desktop-841) {
       width: 60%;
       left: 30%;
     }
    @include mq($until: desktop) {
      padding: 2rem;
      top: 25%;
    }
    @include mq($until: leptop-728) {
      width: 70%;
      left: 20%;
    }
    @include mq($until: leptop-641) {
          width: 70%;
          left: 15%;
          top: 20%;
    }
    @include mq($until: mobile-541) {
      width: 90%;
      left: 12%;
    }
  }
}

h4.title-download-txt-nopadding {
  padding-left: 0rem;
  padding-top: 0rem;
  padding-bottom: 2rem;
   @include mq($until: desktop) {
     font-size: 1.15rem;
     padding-bottom: 1rem;
   }
   @include mq($until: mobile-541) {
    font-size: 1rem;
   }
   @include mq($until: mobile-415) {
     font-size: 1.25rem;
   }
   @include mq($until: mobile) {
     font-size: 1rem;
   }
}

td.no-border-l {
  border-top: 0px solid #dee2e6 !important;
  font-size: 1.5rem;
  @include mq($until: wide-1281) {
    font-size: 1.25rem;
  }
   @include mq($until: desktop) {
    padding: .5rem !important;
   }
   @include mq($until: mobile) {
     font-size: .875rem;
   }
}
td.td-30, td.td-70 {
  @include mq($until: desktop) {
    font-size: 1rem;
  }
  @include mq($until: mobile) {
    font-size: .875rem;
  }

  }

a.mail-add {
  color: $black;
  display: block;
  text-decoration: none !important;
}

a.mail-add:hover {
  text-decoration: underline !important;
  color: map-get($color-blue-map, $blue-600);
}