footer.footer_main {
  width: 100%;
  padding-top: 2.5rem;
  max-width: 1366px;
  margin: auto;

  @include mq($until: leptop-769) {
    padding-top: 2rem;
  }

  @include mq($until: mobile-491) {
    padding-top: 1rem;
  }

  @include mq($until: mobile-415) {
    padding-top: 0rem;
  }

  .footer_map {
    width: 100%;
  }
}

// footer_map_short
.footer_main_1366 {
  max-width: 1366px;
  margin: auto;
}

.footer_map_short {
  margin: 0 auto;
  max-width: 1366px;
  padding-top: 4rem;
}

.footer_map {
  display: flex;
  flex-direction: row;

  @include mq($until: tablet) {
    flex-direction: column;
  }
}

.footer_logo {
  width: 40%;
  height: 12.5rem;
  text-align: center;
  background-image: linear-gradient(141deg, #10118c, #0b69d4 35%, #73c1d2 72%, #d0f6c2);
  // background: #003bde;
  order: 1;

  @include mq($until: desktop) {
    height: 9rem;
  }

  @include mq($until: tablet) {
    order: 2;
    width: 100%;
  }


  &::before {
    content: "";
    width: 0px;
    height: 100%;
    vertical-align: middle;
    display: inline-block;
  }

  a.logo_white_footer {
    vertical-align: middle;
  }
}

.footer_menu {
  width: calc(100% - 40%);
  padding: 1rem 3rem;
  order: 2;

  @include mq($until: desktop) {
    padding: 0rem 3rem;
  }

  @include mq($until: tablet) {
    order: 1;
    width: 100%;
    paddingm: 0rem;
  }

  @include mq($until: mobile-415) {
    padding: 2rem;
  }

  // @include mq($until: mobile-415) {
  //   padding: 2rem 1rem;
  // }
}

.footer_menu_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;

  @include mq($until: wide-1400) {
    justify-content: space-between;
  }

  @include mq($until: tablet) {
    padding-bottom: 2rem;
  }

  @include mq($until: mobile-415) {
    padding-bottom: 0rem;
  }
}

ul.footer_menu_list {
  text-align: left;

  li {
    a.footer_menu_font {
      font-size: .875rem;
      line-height: 2rem;
      color: #1a051d;
      transition: .3s;

      @include mq($until: leptop-769) {
        line-height: 2rem;
      }


      &:hover {
        color: #04146f;
        transition: .3s;
      }
    }

    a.footer_menu_font_bold {
      font-weight: $font-weight-bold;
    }
  }
}

.footer_copyright {
  padding-top: 1rem;
  padding-bottom: 1.5rem;
  padding-left: .75rem;

  @include mq($until: tablet) {
    text-align: center;
    padding: 1rem;
  }

  >p {
    font-size: .75rem;
    color: #999999;
    letter-spacing: 0;

    @include mq($until: tablet) {
      font-size: .875rem;
    }

    @include mq($until: mobile) {
      font-size: .75rem;
    }


    >a {
      color: #999999;
      font-weight: $font-weight-bold;


      &:hover {
        color: #666666;

      }
    }
  }
}

// footer_copyright_short
.footer_copyright_short {
  max-width: 1366px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .products {
    footer.footer_main {
      padding-top: 178px;
    }

    // section.section-summary:last-of-type {
    //   padding-top: 113px;
    //   padding-bottom: 124px;
    // }
  }

  .plateform {
    footer.footer_main {
      padding-top: 113px;
    }

    section.section-summary:last-of-type {
      padding-top: 104px;
      padding-bottom: 104px;
    }
  }

  .download {
    footer.footer_main {
      padding-top: 178px;
    }

    section.section-mission:last-of-type {

      .padding-space.padding-space-width.padding-space-down,
      .padding-space.title-download.padding-space {
        background: linear-gradient(to bottom, white 0%, white 40%, #f8f8f9 40%, #f8f8f9 100%);

        @include mq($until: leptop-769) {
          background: linear-gradient(to bottom, white 0%, white 20%, #f8f8f9 20%, #f8f8f9 100%);
        }
      }
    }
  }

  .privacy {
    footer.footer_main {
      padding-top: 157px;
    }
  }

  .about {
    footer.footer_main {
      padding-top: 141px;
    }

    section.section-responsibility:last-of-type {
      padding-top: 135px;
      padding-bottom: 131px;
    }

  }

}