.content-inner-qa {
  @include mq($until: wide-1400) {
    padding: 2rem;
  }

  @include mq($until: desktop) {
    padding: 1rem 2rem;
  }

  @include mq($until: leptop-769) {
    padding-bottom: 1rem;
  }

  @include mq($until: mobile) {
    padding: 1rem;
  }

  >h2 {
    padding-bottom: 1rem;

    @include mq($until: mobile-415) {
      font-size: 1.5rem;
    }
  }

}

.section-qa {
  // display: flex;
  // flex-direction: row;
  // justify-content: space-around;
  background-image: linear-gradient(to right, #f8f8f9, #f8f8f9 50%, #FFF 50%, #FFF);

  @include mq($until: leptop-769) {
    background-image: none;
  }
}

.padding-space-width-gray {
  background-color: #f8f8f9;
  width: 20%;

  @include mq($until: wide-1400) {
    width: 10%;
  }

  @include mq($until: desktop-1025) {
    width: 5%;
  }

  @include mq($until: leptop-769) {
    width: 0%;
  }
}

.padding-space-width-white {
  background-color: $white;
  width: 20%;

  @include mq($until: wide-1400) {
    width: 10%;
  }

  @include mq($until: desktop-1025) {
    width: 5%;
  }

  @include mq($until: leptop-769) {
    width: 0%;
  }
}

.content-inner-s-nopadding {
  // width: 1044px;
  // max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  width: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;

  @include mq($until: wide-1400) {
    width: 80%;
  }

  @include mq($until: desktop-1025) {
    width: 90%;
  }

  @include mq($until: leptop-769) {
    flex-direction: column;
    width: 100%;
  }

  .question {
    background-color: #f8f8f9;
    padding: 2.5rem;
    padding-right: 2rem;
    width: 390px;

    @include mq($until: desktop-1025) {
      padding: 2rem;
      padding-left: 3.5rem;
      width: 360px;
    }

    @include mq($until: desktop-877) {
      padding: 1rem;
      padding-left: 2.5em;
    }

    @include mq($until: leptop-769) {
      width: 100%;
      padding: 1rem 4rem;

    }

    @include mq($until: tablet) {
      padding: 1rem 3rem;
    }

    @include mq($until: mobile-491) {
      padding-bottom: 1rem;
    }

    @include mq($until: mobile-491) {
      padding: 1rem 2rem;
    }

  }

  .answer {
    background-color: $white;
    padding: 2.5rem 4rem;
    width: 70%;

    @include mq($until: desktop-1025) {
      padding: 2rem;
    }

    @include mq($until: desktop-877) {
      padding: 1rem 2rem 1rem 2rem;
    }

    @include mq($until: leptop-769) {
      width: 100%;
      padding: 1rem 4rem;
    }

    @include mq($until: tablet) {
      padding: 1rem 3rem;
    }

    @include mq($until: mobile-415) {
      padding: 1rem 2rem;
    }

    @include mq($until: mobile) {
      padding: 1rem;
    }

    >p {
      @include mq($until: mobile-360) {
        font-size: .75rem;
      }
    }
  }
}

.section-qa {
  &+.section-qa {
    margin-top: 1.5rem;
    background-color: #f8f8f9;

    @include mq($until: leptop-769) {
      margin-top: 0rem;
    }
  }
}

.question {
  p.qa-txt-title {
    font-size: 1.25rem;
    color: map-get($color-blue-map, blue-400);
    letter-spacing: 0;
    text-indent: -1.5rem;

    @include mq($until: wide-1400) {
      font-size: 1.15rem;
    }

    @include mq($until: desktop-877) {
      font-size: 1rem;
    }

    @include mq($until: mobile-415) {
      text-indent: -1rem;
    }

    @include mq($until: mobile) {
      font-size: .875rem;
    }
  }
}

.ans-txt-list-num {
  padding-bottom: 1rem;

  @include mq($until: mobile-415) {
    padding-left: 1rem;
  }

  li {
    line-height: 2.5;

    @include mq($until: mobile-376) {
      line-height: 2rem;
    }

    @include mq($until: mobile) {
      font-size: .75rem;
      line-height: 1.5rem;
    }
  }
}

p.ans-subtit {
  color: map-get($color-blue-map, blue-400);
  font-weight: $font-weight-bold;

  @include mq($until: mobile) {
    font-size: .875rem;
  }
}

.no-border-s {
  font-size: .875rem;
  font-weight: $font-weight-normal;
  border-top: 0px solid #dee2e6 !important;
  padding: .5rem !important;

  @include mq($until: mobile) {
    font-size: .75rem;
  }
}

.td-30 {
  width: 30%;

  @include mq($until: mobile-491) {
    width: 40%;
  }
}


.td-70 {
  width: 70%;

  @include mq($until: mobile-491) {
    width: 60%;
  }
}


.note-list {
  padding-top: 1rem;
  font-size: .875rem;
  line-height: 2;
  padding-left: 1rem;

  @include mq($until: mobile) {
    font-size: .75rem;
  }
}

.ans-table-bank {
  &+.ans-table-bank {
    padding-top: 1rem;
  }
}

.ans-txt-content {
  >p {
    @include mq($until: mobile) {
      font-size: .75rem;
    }
  }
}